<!--html-->
<template>
	<div id="offCanvasWrapper" class="mui-off-canvas-wrap mui-draggable">
		<!---->
		<aside id="offCanvasSide" class="mui-off-canvas-left">
			<div id="offCanvasSideScroll" class="mui-scroll-wrapper">
				<div class="mui-scroll">
					<div class="title"></div>
					<div class="content">
						 1.(drag)2.; 3.
						<span class="android-only">4.Androidback5.Androidmenu
							</span>
						<p style="margin: 10px 15px;">
							<button id="offCanvasHide" type="button" class="mui-btn mui-btn-danger mui-btn-block" style="padding: 5px 20px;"></button>
						</p>

					</div>
					<div class="title" style="margin-bottom: 25px;"></div>
					<ul class="mui-table-view mui-table-view-chevron mui-table-view-inverted">
						<li class="mui-table-view-cell">
							<!--<a class="mui-navigate-right" href="#/index/ask">
								
							</a>	-->
							<button class="mui-navigate-right" @click="linkTo(0)"></button>
						</li>
						<li class="mui-table-view-cell">
							<!--<a class="mui-navigate-right" href="#/index/share">
								
							</a>-->
							<button class="mui-navigate-right" @click="linkTo(1)"></button>
						</li>
						<li class="mui-table-view-cell">
							<!--<a class="mui-navigate-right" href="#/index/job">
								
							</a>-->
							<button class="mui-navigate-right" @click="linkTo(2)"></button>
						</li>
						<li class="mui-table-view-cell">
							<!--<a class="mui-navigate-right" href="#/index/good">
								
							</a>-->
							<button class="mui-navigate-right" @click="linkTo(3)"></button>
						</li>
					</ul>
				</div>
			</div>
		</aside>
		<!---->
		<div class="mui-inner-wrap mui-slider-group">
			<header class="mui-bar mui-bar-nav">
				<a href="#offCanvasSide" class="mui-icon mui-action-menu mui-icon-bars mui-pull-left"></a>
				<a class="mui-action-back mui-btn mui-btn-link mui-pull-right"></a>
				<h1 class="mui-title">div</h1>
			</header>
			<div id="offCanvasContentScroll" class="mui-content mui-scroll-wrapper">
				<div class="mui-scroll">
					<router-view></router-view>
					<div class="mui-content-padded" id='mui-content-padded'>
						<carousel></carousel>
						<list></list>
					</div>
				</div>
			</div>
			
			<!-- off-canvas backdrop -->
			<div class="mui-off-canvas-backdrop"></div>
		</div>
	</div>
</template>
<!--js-->
<script>
	import carousel from "./index/carousel.vue";
	import list from "./index/list.vue";
	module.exports = {
		components: {
			carousel,
			list,
		},
		mounted() {
			mui.init();
			//
			var offCanvasWrapper = mui('#offCanvasWrapper');
			//
			var offCanvasInner = offCanvasWrapper[0].querySelector('.mui-inner-wrap');
			//
			var offCanvasSide = document.getElementById("offCanvasSide");

			//
			var moveTogether = false;
			
			 //
//			document.getElementById('offCanvasShow').addEventListener('tap', function() {
//				offCanvasWrapper.offCanvas('show');
//			});
			 //
			document.getElementById('offCanvasHide').addEventListener('tap', function() {
				offCanvasWrapper.offCanvas('close');
			});
			
			//
			mui('#offCanvasSideScroll').scroll();
			mui('#offCanvasContentScroll').scroll();
			
			mui('body').on('tap','a',function(){document.location.href=this.href;});
			//
		},
		methods:{
			linkTo(page){
//				console.log(page)
				//
				switch(page){
					case 0:
					window.location.href = "#/index/ask";
					break;
					case 1:
					window.location.href = "#/index/share";
					break;
					case 2:
					window.location.href = "#/index/job";
					break;
					case 3:
					window.location.href = "#/index/good";
					break;
				}
//				
				this.offHide()
			},
			offHide(){
				var offCanvasWrapper = mui('#offCanvasWrapper');
				offCanvasWrapper.offCanvas('show');
				$("#mui-content-padded").css({display:'none'})
			}			
		}
	}
</script>
<!--css sass less-->
<!--scoped lang="scss"-->
<style>
	html,
	body {
		background-color: #efeff4;
	}
	/*p {
		text-indent: 22px;
	}*/
	
	span.mui-icon {
		font-size: 14px;
		color: #007aff;
		/*margin-left: -15px;*/
		padding-right: 10px;
	}
	
	.mui-off-canvas-left {
		color: #fff;
	}
	
	.title {
		margin: 35px 15px 10px;
	}
	
	.title+.content {
		margin: 10px 15px 35px;
		color: #bbb;
		text-indent: 1em;
		font-size: 14px;
		line-height: 24px;
	}
	
	input {
		color: #000;
	}
	/*#offCanvasContentScroll{
		margin-bottom:50px;
	}*/
	.mui-content-padded {
		margin: 0;
	}
</style>


